'use client';

import { useState } from 'react';
import Link from 'next/link';
import styles from './Navbar.module.css';

export default function Navbar() {
    // Logic for mobile menu toggle could go here, for now relying on CSS hover for dropdowns
    // but demonstrating interactivity if needed.

    return (
        <nav className={styles.navbar}>
            <div className={styles.container}>
                <Link href="/" className={styles.logo}>
                    IES Cura Valera
                </Link>

                <ul className={styles.navLinks}>
                    <li className={styles.navItem}>
                        <Link href="/">Inicio</Link>
                    </li>
                    <li className={styles.navItem}>
                        <span>El Centro</span>
                        <div className={styles.dropdown}>
                            <Link href="/centro" className={styles.dropdownItem}>Historia</Link>
                            <Link href="/organigrama" className={styles.dropdownItem}>Organigrama</Link>
                        </div>
                    </li>
                    <li className={styles.navItem}>
                        <span>Oferta Educativa</span>
                        <div className={styles.dropdown}>
                            <Link href="/eso" className={styles.dropdownItem}>ESO</Link>
                            <Link href="/bachillerato" className={styles.dropdownItem}>Bachillerato</Link>
                            <Link href="/fp" className={styles.dropdownItem}>Formaci√≥n Profesional</Link>
                        </div>
                    </li>
                    <li className={styles.navItem}>
                        <Link href="/contacto">Contacto</Link>
                    </li>
                </ul>
            </div>
        </nav>
    );
}
